import React from 'react'

const defaultProps = {
  color: '#ffffff',
  orientation: 'floor',
}

const Floor = ({ sizeX, sizeY, sizeZ, color, orientation }) => {
  const floorX = 5
  const floorY = 3
  const floorZ = 2
  const material = <meshLambertMaterial attach="material" color={color} />
  const materialShadow = (
    <meshLambertMaterial
      attach="material"
      color={color}
      transparent={true}
      opacity={0.2}
    />
  )
  const props =
    orientation === 'floor'
      ? { 'rotation-x': -Math.PI / 2, 'position-y': -0.05 }
      : {
          'rotation-x': 0,
          'position-z': -floorZ / 2 - 0.05,
          'position-y': floorY,
        }

  return (
    <group>
      <mesh {...props} receiveShadow={true}>
        <planeBufferGeometry
          attach="geometry"
          args={[(floorX + 1) * 5, (sizeY + 1) * 5]}
        />
        {materialShadow}
      </mesh>
      <mesh {...props} receiveShadow={false} castShadow={true}>
        <planeBufferGeometry
          attach="geometry"
          args={[(floorX + 1) * 5, (sizeY + 1) * 5]}
        />
        {material}
      </mesh>
    </group>
  )
}
Floor.defaultProps = defaultProps
export default Floor
