import React from 'react'
import { ThreeBSP } from 'three-js-csg-es6'
import * as THREE from 'three'

const defaultProps = { color: '#ffffff' }
const STEP = 0.125

const min = 3

const sections = (total) => Math.ceil(total / min)

const makeHoles = ({ sizeX, sizeY }) => {
  const xSections = sections(sizeX)
  const ySections = sections(sizeY)
  const xSize = sizeX / xSections
  const ySize = sizeY / ySections
  return new Array(ySections).fill().flatMap((_, j) => {
    return new Array(xSections).fill().flatMap((_, i) => {
      return {
        xSize: xSize - STEP,
        ySize: ySize - STEP,
        x: (i + 1) * xSize - xSize / 2,
        y: (j + 1) * ySize - ySize / 2,
        z: STEP,
      }
    })
  })
}

const Shelf = ({ sizeX, sizeY, sizeZ, color }) => {
  const material = <meshStandardMaterial attach="material" color={color} />
  const materialShadow = (
    <meshStandardMaterial
      attach="material"
      color={color}
      transparent="true"
      opacity="0.3"
    />
  )
  const madeHoles = makeHoles({ sizeX, sizeY })
  const holes = madeHoles.map(({ xSize, ySize, x, y, z }) => {
    const h = new THREE.Mesh(new THREE.BoxGeometry(xSize, ySize, sizeZ))
    h.position.x = x
    h.position.y = y
    h.position.z = z
    return h
  })

  const box = new THREE.Mesh(
    new THREE.BoxGeometry(sizeX + STEP, sizeY + STEP, sizeZ)
  )
  box.position.x = sizeX / 2
  box.position.y = sizeY / 2
  // const hole = new THREE.Mesh(
  // new THREE.BoxGeometry(sizeX - STEP, sizeY - STEP, sizeZ)
  // )
  // hole.position.z = STEP

  const bBSP = new ThreeBSP(box)
  const sub = holes.reduce(
    (whole, hole) => whole.subtract(new ThreeBSP(hole)),
    bBSP
  )
  const newGeometry = sub.toGeometry()
  return (
    <group position-y={STEP / 2}>
      <mesh castShadow={true} receiveShadow={false} geometry={newGeometry}>
        {material}
      </mesh>
      <mesh castShadow={false} receiveShadow={true} geometry={newGeometry}>
        {materialShadow}
      </mesh>
    </group>
  )
}

Shelf.defaultProps = defaultProps
export default Shelf
