import React from 'react'
import { Canvas } from 'react-three-fiber'
import Scene from 'src/components/Scene'
import OrbitControls from 'src/components/Controls'
import * as THREE from 'three'
import { Controls, useControl } from 'react-three-gui'

const sizeZ = 2
const materials = {
  Белый: '#ffffff',
  Черный: '#333333',
  Сосна: '#fcd0aa',
}

const App = () => {
  const sizeX = useControl('Ширина', {
    type: 'number',
    value: 5,
    min: 3,
    max: 9,
  })
  const sizeY = useControl('Высота', {
    type: 'number',
    value: 3,
    min: 3,
    max: 6,
  })
  const material = useControl('Материал', {
    type: 'select',
    items: Object.keys(materials),
  })
  const color = materials[material]
  const sizes = { sizeX, sizeY, sizeZ }
  return (
    <>
      <Canvas
        gl={{ antialias: true, alpha: false }}
        camera={{ position: [sizeX / 2, sizeY + 3, sizeZ + 3] }}
        onCreated={(args) => {
          const { gl } = args
          gl.setClearColor(new THREE.Color('#ffffff'))
          gl.shadowMap.enabled = true
          gl.shadowMap.type = THREE.PCFSoftShadowMap
        }}
      >
        <ambientLight intensity={0.3} />
        <pointLight
          args={[0xffffff, 0.5]}
          position={[-sizeX / 2 - 2, 7, 10]}
          castShadow={true}
          shadow-mapSize-width={1024}
          shadow-mapSize-height={1024}
        />
        <pointLight
          args={[0xffffff, 0.5]}
          position={[sizeX / 2 + 2, 7, 10]}
          castShadow={true}
          shadow-mapSize-width={1024}
          shadow-mapSize-height={1024}
        />
        <Scene {...{ ...sizes, color }} />
        <OrbitControls {...sizes} />
      </Canvas>
      <Controls />
    </>
  )
}

export default App
